import React, { useState } from 'react'
import { graphql } from 'gatsby'
import moment from 'moment'

import Layout from './../layout'
import { SEO, StripeBar, Banner, Button } from './../components'
import { SubmitEvent, FindEvents, PulseEvent } from './../containers/Pulse'
import { Date as DateComp, Content } from './../containers/Pulse/style'
import CalendarImg from './../images/on-the-pulse-calendar.svg'
import { useLocation } from '@reach/router'
import { format, isAfter } from 'date-fns'
import frLocale from 'date-fns/locale/fr'

interface IPulsePageProps {
  location: {
    pathname: string
  }
  data: any
  pageContext: {
    language: string
    title: string
    description: string
    section1: string
    seoKeyword: string
    seoDescription: string
  }
}

export default ({
  location,
  data,
  pageContext: {
    language,
    title,
    description,
    section1,
    seoKeyword,
    seoDescription,
  },
}: IPulsePageProps) => {
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')

  const [filter, setFilter] = useState({
    from: '',
    search: '',
  })

  const handleSearch = (values: any) => {
    setFilter(values)
  }
  return (
    <Layout location={location}>
      <SEO
        pathname={location.pathname}
        title={title}
        description={seoDescription}
        keyword={seoKeyword}
      />
      <Banner colors={['#397897', '#00587F', '#80D0DB']}>
        <h1>{title}</h1>
        <h2 className='mt-4'>{description}</h2>
      </Banner>
      <StripeBar colors={['#E58630', '#DA5027', '#D1932A', '#D16428']} />
      <SubmitEvent image={CalendarImg}>
        <h2>{section1}</h2>
        <div className='mt-5'>
          <Button type='url' to='/shareEvent'>
            {isFr
              ? 'Soumettez votre événement à venir ici'
              : 'Submit your upcoming event here'}
          </Button>
        </div>
      </SubmitEvent>
      <FindEvents onFindEvents={handleSearch} />
      <div className='container'>
        <Content>
          <DateComp className='mb-5'>
            {filter.from
              ? format(new Date(filter.from), 'MMM yyyy', {
                  locale: isFr ? frLocale : undefined,
                })
              : format(new Date(), 'MMM yyyy', {
                  locale: isFr ? frLocale : undefined,
                })}
          </DateComp>
          {data.allMarkdownRemark.edges
            .filter(
              ({ node }) =>
                (!filter.from ||
                  isAfter(
                    new Date(node.frontmatter.eventStartDate),
                    new Date(filter.from)
                  )) &&
                // node.frontmatter.eventDescription
                // .toLowerCase()
                // .includes(filter.search.toLowerCase()) ||
                ((node.frontmatter.title
                  .toLowerCase()
                  .includes(filter.search.toLowerCase()) &&
                  language === 'En') ||
                  (node.frontmatter.titleFr &&
                    node.frontmatter.titleFr
                      .toLowerCase()
                      .includes(filter.search.toLowerCase()) &&
                    language === 'Fr'))
            )
            .slice(0, 5)
            .map(({ node: { frontmatter } }, index: number) => {
              return (
                <PulseEvent
                  language={language}
                  key={`${index}key`}
                  {...frontmatter}
                />
              )
            })}
        </Content>
      </div>
    </Layout>
  )
}

export const indexPageQuery = graphql`
  query pulsePageQuery {
    allMarkdownRemark(
      filter: {
        fileAbsolutePath: { regex: "/events/i" }
        frontmatter: { published: { eq: true } }
      }
    ) {
      edges {
        node {
          frontmatter {
            eventCategory
            eventCost
            eventDescription
            eventDescriptionFr
            eventEndDate
            eventStartDate
            title
            titleFr
            eventUrl
            organizerEmail
            organizerName
            organizerPhone
            organizerWebsite
            published
            venueAddress
            venuePhone
            venueCity
            venuePostalCode
            venueProvince
            venueWebsite
          }
        }
      }
    }
  }
`
